body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}.banner{
  height: 500px!important;
  width: 100%!important;
  margin-top:0!important;
  background-image: url('images/banner.png');
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
    
}.cardflow{
  margin-left: -10%!important;
}
.profileCard{
  position: absolute;
  margin-top: 70%; 
  margin-left:30%;
  padding: 10px;

}
.center-pic{
  margin-left: 60%!important;
  border:4px solid #2c91bf;
}.userinfo{
  margin-left: 0%!important;
  margin-top: -25%;
}h3.subtitle{
  font-size: 1.1em;
  font-weight: bold;
}h3.subtitle p{
  margin-top: -4%;
}
.justify-align{
  text-align: justify;
}
.about{
  width: 70%;
  text-align: center;
  padding: 10px;
  margin-top: 13%;
}
.context{
  width: 100%!important;
}

.tech-tools{
  width: 70%;
  text-align: center;
  padding: 10px;
  margin-top: 2%;
}
.tech-tool-item{
  margin: 10% 0 0 0;
}
.tech-tool-desc{  
  margin: 4% 0 0 0;
}
.socialList{
  list-style-type: none;
  margin-top: -10%;
  
}
.socialList li{
  display: inline-block;
  margin: 5px;
}
.socialList li a{
  color: #333;
}
.icon:before{
  font-size: 3em;
}
.icon:hover{
  color:#2c91bf;
  -webkit-transition: 600ms;
  -o-transition: 600ms;
  transition: 600ms
}
footer{
  clear: both;
  height: 60px;
  text-align: center;
}
.more-info{
  width: auto;
  position: relative;
  margin-top: -1%;
  margin-left: -2%;
  
}
.more-info li{
  display: inline-block;
  margin: 10px;
}
.more-info li a{
  color: #2c91bf;
}


